const CenteredContainer = styled.div`
    display: flex;
    justify-content: center;
    max-width: 600px;
    width: auto;
    margin: auto;
    overflow: auto;
`;

const CenteredContainerComponent = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    width: auto;
    margin: 30px 30px 10px 30px;
`;
const CenteredContainerSubComponent = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    width: auto;
    margin: 0px 30px 10px 30px;
`;

const CenteredContainerTextComponent = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    width: auto;
    margin: 10px 30px;
`;

const CenteredContainerDummy = styled.div`
    justify-content: center;
    align-items: center;
    max-width: 600px;
    width: auto;
    height: 10.0em;
`;

const ImageContainer = styled.div`
    width: 600px;
    height: 450px;
    max-width: 100%;
    overflow: hidden;
`;

const Image = styled.img`
    width: 100%;
    height: 100%; 
    object-fit: cover; 
    object-position: center 30%;
`;

const KakaoImage = styled.img`
    width: 38px;
`;

const ImageDownload = styled.button`
    position: absolute;
    top: 400px;
    right: 0px;
    padding: 18px 18px;
    background : transparent;
    border-style : none;
    color: rgb(255, 255, 255);
    font-size: 0.9em;
    font-family: P-Bold;

    @media (min-width:601px) {
        right: unset; 
        left: calc(50% + 190px); 
    };
`

const HashTagContainer = styled.div`
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    max-width: 90%;
    margin: 0px 30px 0px;

    @media (max-width:381px) {
        margin: 0px 70px;
        flex-wrap: wrap;
    };
    @media (min-width: 440px) and (max-width: 600px) {
        margin: 0px calc(45px + 50 * (100vw - 440px) / 160);
    }
    @media (min-width: 600px) {
        margin: 0px 120px;
    }
`

const HashTag = styled.div`
    display: flex;
    align-items: center;
    height: 33px;
    font-family: P-Bold;
    font-size: 0.9em;
    border-radius: 100px;
    background: rgb(241, 243, 248);
    padding: 0px 12px;
    margin: 6px 3px;
    white-space: nowrap;
`
const DivComponent = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: auto;
    max-width: 600px;
    background-color: ${props => props.bgcolor || ''};
    width: 100%;
    overflow: auto;
`

const KakaoContainerComponent = styled.div`
    width: 100%;
    position: fixed;
    z-index: 11;
    bottom: -3px;
    left: 50%; // 화면 가운데에 위치하도록 변경
    transform: translateX(-50%); // 컨테이너의 가운데를 기준으로 이동하도록 변경
    font-size: 15px;
    height: 50px;
    max-width:600px;
`

export default Result