import FormComponent from "../components/FormComponent"
import ContainerComponent from "../components/ContainerComponent"
import TextComponent from "../components/TextComponent"
import QuestionButtonComponent from "../components/QuestionButtonComponent"
import { Text } from "../common/constant"
import { routingIE, routingTF } from "../common/constant"
import { useRoutingFunction } from "../hooks/userouting"
import { useStore } from "../state/usestore"

import styled from "styled-components"

const CenteredContainer = styled.div`
  display: flex;
  justify-content: center;
  /* align-items: center; */
  height: 100vh;
  background-color: #FAFAFA;
  max-width: 600px;
  width: auto;
  margin: auto;
`;

const QuestionTF = (props)=> {
    const index = routingTF.indexOf(props.number)
    const moveNextQuestions = useRoutingFunction(`${props.number}`,"")
    const state = useStore(state => state)
    const setAnswerState = (value) => state[`set_TF_${props.number}`](value)

    return <CenteredContainer>
    <QStyledForm>
    <HeaderContainerComponent>
    <TextComponent 
        msg = {`${props.number}/12`}
        fontsize = {"1.2em"}>
        </TextComponent>
    </HeaderContainerComponent>
        <QContainerComponent>
            <TextComponent 
            msg = {Text[`question_type_TF_${routingIE[index]}`]}
            fontsize = {"1.4em"}
            txtwrap={"wrap"} >
            </TextComponent>
        </QContainerComponent>

        
            <QuestionButtonComponent 
            msg = {Text[`q_type_TF_${routingIE[index]}_answer_T`]}
            onClick = {() => {
                moveNextQuestions()
                setAnswerState("T")
            }}>
            </QuestionButtonComponent>
       
        
            <QuestionButtonComponent 
            msg = {Text[`q_type_TF_${routingIE[index]}_answer_F`]}
            onClick = {() => {
                moveNextQuestions()
                setAnswerState("F")
            }}>
            </QuestionButtonComponent>
        
    </QStyledForm>
    </CenteredContainer>
}

const QStyledForm = styled.form`
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /* height: 800px; */
    max-width: 600px;
    margin-top : 200px;
    width: 100%;
`

const QContainerComponent = styled.div`
    display: flex;
    flex-direction : column;
    margin: 10px 10px 10px 20px;
    justify-content: center;
    width: auto;
`

const HeaderContainerComponent = styled.div`
    display: flex;
    flex-direction : column;
    width : 100%;
    margin-left : 20px;
    align-items : flex-start;
`;

export default QuestionTF